import style from './LoveStory.module.scss';

import firstPhoto from '../../../assets/img/Photos/firstStoryPhoto.JPG';
import secondPhoto from '../../../assets/img/Photos/secondStoryPhoto.JPG';

export const LoveStory = () => {

    return (
        <div className="pageContainer">
            <h1 className={style.header}>Bride and Groom</h1>
            <div className={style.storyContainer}>
                <div className={style.storyCell}>
                    <div className={style.storyPhoto}>
                        <img src={firstPhoto} alt='Kam'></img>
                    </div>
                    <div className={style.story}>
                        <h5 className={style.names}>Michellyn Kamille Vijandre Rosario</h5>
                        <p>How come that Kam Rosario deserves Maikee Sheen Doop?
 Yan ang iniisip ko before kay Maikee. He is someone na never kung inexpect na darating sa buhay ko. I am thankful sa mga friends namin na nasa fulltime service na nagpakilala sa aming dalawa. We started as friends, we call each other “Coffee” kasi we’re both coffee lover. Hindi na ako nag-assume ng more than friends dahil nasa Luzon ako and nasa Cebu siya at never pa akong nakarating doon! And the more na nakilala ko siya mas na appreciate ko yung theocratic background niya parang feeling ko hindi ako yung sister na babagay sa kanya. But Maikee is the best brother na nakilala ko, he is spiritually matured and never siyang na-fail na iparamdam sa akin na I deserve to be treated right kahit ang layo namin sa isat-isa.
Ang laki ng fear ko sa commitment. Ilang beses narin nabigo at maraming naging lesson. Pero naging totoo sa akin na alam na alam ni Jehova ang tamang panahon para ibigay ang hiling Ng puso ko. Nang dumating si Maikee sa buhay ko, nag karuon ako ng tunay na kaibigan at totoong nag mamahal sa akin na tanggap ang buong ako . Nagawa niyang ipag sigawan ako sa mundo niya, mundo ko at pinaka mahalaga nagawa niyang humarap sa papa ko, na siya lang ata nakagawa nun! Sigurado ako, si Maikee Yung brother na ni ready ni Jehova para sa akin,nakilala ko after 28 years. I'm so happy na natapos din ang matagal kong pag hihintay para sa tamang tao.  Totoo nga, patience is the key.
I failed before, Pero lahat Ng yun may dahilan, para makilala ko pa si Maikee. I'm looking forward sa mga spiritual goals namin together as  "Need greater".  As my future husband, if marrying you ay parang lifetime prison, I want to stay there forever as long as I'm with you . Nang dumating ka sa buhay ko, naging totoo sa akin na Jehovah wants not only the better but the best brother for me and that's you ,Maikee. I'll always love you. 
I'm so proud to say that , Yes!  Kam Rosario deserves Maikee Sheen Doop. ❤️</p>
                    </div>
                </div>
                <div className={style.storyCell}>
                    <div className={style.story}>
                        <h5 className={style.names}>Maikee Sheen Parcon Doop</h5>
                        <p>Kam and I were friends from a far distance. Yes, we may have a great distance from each other, but we have proved that communication is the best key to any relationship. It started as a simple conversation and ended up to where we are now. I never thought that I would meet someone who has the same interest as me. I like how she served Jehovah at a young age like me, cooks the food I like the most and most importantly. I love how she loves to travel and drive much as me. Its uncanny really, how we focus our lives in the ministry and it brings us together. Its funny how it took me a while to meet Kam, but I know that Jehovah reserved me the best, and that's Kam. My best friend, my confidant and my equal.</p>
                    </div>
                    <div className={style.storyPhoto}>
                        <img src={secondPhoto} alt='Kee'></img>
                    </div>
                </div>
            </div>
            
        </div>
    );
}