import style from './Venue.module.scss';
import React, { useState } from "react";
import { Map, Marker, ZoomControl  } from "pigeon-maps";

import BankDetails from './BankDetails';


export const Venue = () => {
    return (
        <div className={style.pageContainer}>
            <div className={style.mapLocation}>
                <Map className={style.location} defaultCenter={[15.45164262738101, 120.96197799999999]} 
                    defaultZoom={18} metaWheelZoom='true'>
                    <Marker width={50} anchor={[15.45164262738101, 120.96197799999999]} />
                    <ZoomControl />
                </Map>
            </div>
            <h1>Venue</h1>
            <h2>Lakewood Golf and Country Club, Inc.</h2>
            <p>Maharlika Highway, Sumacab Este, Cabanatuan City, Nueva Ecija, Philippines 3100</p>
            <p>January 23, 2023 - 2:00pm</p>
            <h2>Zoom Guests</h2>
            <p>Zoom link will be given on the day of the event.</p>
            <BankDetails/>

        </div>
    );
}